.home {
    width: 50%;
    height: auto;
    overflow: hidden;
    position: absolute;
    top: 25%;
    left: 25%;
}
.homeButton {
    width: 50%;
    height: auto;
    margin: auto;
    overflow: hidden;
}
.estiloTag {
    font-family: 'Nothing You Could Do', cursive;
    margin: 0;
    padding: 0;
    color: grey;
    font-size: 14px;
    margin-left: 0px;
    margin-top: 5px;
}
.agujero .estiloTag {
    margin-bottom: 100px;
}
.nombre span {
    font-family: 'Nothing You Could Do', cursive;
    color: #00e8fc;
    font-weight: bold;
    font-size: 64px;
    -ms-transform: translateX(-2rem);
    transform: translateX(-2rem);
    -webkit-transform: translateX(-2rem);
    opacity: 0;
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-name: animacionTitulo;
    animation-name: animacionTitulo;
}
.nombre span:nth-child(2) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}
.nombre span:nth-child(3) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
}
.nombre span:nth-child(4) {
    -webkit-animation-delay: 0.8s;
    animation-delay: 0.8s;
}
.nombre span:nth-child(5) {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}
.nombre span:nth-child(6) {
    -webkit-animation-delay: 1.2s;
    animation-delay: 1.2s;
}
.nombre span:nth-child(7) {
    -webkit-animation-delay: 1.4s;
    animation-delay: 1.4s;
}
.nombre span:nth-child(8) {
    -webkit-animation-delay: 1.6s;
    animation-delay: 1.6s;
}
.nombre span:nth-child(9) {
    -webkit-animation-delay: 1.8s;
    animation-delay: 1.8s;
}
.nombre span:nth-child(10) {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
}
.nombre span:nth-child(11) {
    -webkit-animation-delay: 2.2s;
    animation-delay: 2.2s;
}
.nombre span:nth-child(12) {
    -webkit-animation-delay: 2.4s;
    animation-delay: 2.4s;
}
.nombre span:nth-child(13) {
    -webkit-animation-delay: 2.6s;
    animation-delay: 2.6s;
}
.texto {
    font-size: 24px;
}

/* HOVER */
.estiloTag:hover {
    cursor: default;
}

/* RESPONSIVE DESIGN */
@-webkit-keyframes animacionTitulo {
    0% {
        transform: translateX(-2rem);
        -webkit-transform: translateX(-2rem);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        -webkit-transform: translateX(0);
        opacity: 1;
    }
}
@keyframes animacionTitulo {
    0% {
        transform: translateX(-2rem);
        -webkit-transform: translateX(-2rem);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        -webkit-transform: translateX(0);
        opacity: 1;
    }
}

/* BLACK HOLE */
.star {
    z-index: -1;
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: #00e8fc;
    -webkit-animation: drain 4s ease-in infinite;
    animation: drain 4s ease-in infinite;
    border-radius: 50%;
    opacity: 0;
}
.star:first-of-type {
    top: 0%;
    left: 5%;
}
.star:nth-of-type(2) {
    top: 75%;
    left: 90%;
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
}
.star:nth-of-type(3) {
    top: 90%;
    left: 140%;
    -webkit-animation-delay: 3s;
    animation-delay: 3s;
}
.star:nth-of-type(4) {
    top: 60%;
    left: 1%;
    -webkit-animation-delay: 1.8s;
    animation-delay: 1.8s;
}
.star:nth-of-type(5) {
    top: 0%;
    left: 90%;
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
}
.boton {
    color: black;
    border-color: #d9ffff;
    width: 100px;
    height: 100px;
    background-color: black;
    background-size: 200%;
    -webkit-transition: 0.5s ease;
    -o-transition: 0.5s ease;
    transition: 0.5s ease;
    font-size: 24px;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 10px 1px #d9ffff;
    box-shadow: 0 0 10px 1px #d9ffff;
    -webkit-animation: enterColor 9s ease-in infinite;
    animation: enterColor 9s ease-in infinite;
    z-index: 2;
    position: absolute;
    top: calc(70% - 50px);
    left: calc(50% - 50px);
}

/* HOVER */
.boton:hover {
    cursor: pointer;
    color: #d9ffff;
}

/* ANIMATIONS */
@-webkit-keyframes drain {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    50% {
        opacity: 1;
        -webkit-transform: scale(0.75);
        transform: scale(0.75);
    }

    100% {
        opacity: 0;
        top: 65%;
        left: 50%;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}
@keyframes drain {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    50% {
        opacity: 1;
        -webkit-transform: scale(0.75);
        transform: scale(0.75);
    }

    100% {
        opacity: 0;
        top: 75%;
        left: 50%;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}
@-webkit-keyframes enterColor {
    0% {
        color: black;
    }
    50% {
        color: #d9ffff;
    }
}
@keyframes enterColor {
    0% {
        color: black;
    }
    50% {
        color: #d9ffff;
    }
}

/* RESPONSIVE DESIGN */
@media only screen and (max-width: 640px) {
    .home {
        width: 90%;
        height: auto;
        left: 5%;
    }
    .boton {
        top: calc(75% - 50px);
    }
}
@media only screen and (min-width: 641px) and (max-width: 1080px) {
    .home {
        width: 80%;
        height: auto;
        top: 7%;
        left: 10%;
    }
}
